#page-container{
  width: 80%;
  padding: 0;
  margin: 0 auto;
}

nav ul{
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
  justify-content: center;
  border-bottom: 3px solid #000;
}

nav ul li a{
  display: block;
  width: 200px;
  height: 60px;
  text-align: center;
  line-height: 60px;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 20px;
  text-decoration: none;
  color: #000;
}

nav ul li a:hover{
  background-color: #000;
  color: #fff;
}

.article-list-item{
  display: block;
  color: black;
  text-decoration: none;
  border-bottom: 1px solid black;
}